@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;700&display=swap);
:root{
  --color-accent: rgb(170, 244, 252);
  --color-accent_2: rgb(157, 157, 157);
  --color-accent_3: rgb(199, 225, 255);
  --letter-spacing-big: 0.6rem;
  --letter-spacing-small: 0.25rem;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Navigation{
	margin-bottom: 0.3rem;
}

.nav-links {
	list-style-type: none;
	display: flex;
	justify-content: space-between;
	padding: 0;
	margin: 0;
}

.nav-link {
	padding: 0;
	padding-bottom: 0.5rem;
}

.nav-link a {
	color: white;
	text-decoration: none;
	font-size: 2rem;
}

.active-link {
	color: var(--color-accent) !important;
}

.Navigation li {
	display: inline-block;
	position: relative;
	padding-bottom: 2px;
	padding-top: 2px;
}

.Navigation li:after,
.Navigation li:before {
	content: '';
	display: block;
	margin: auto;
	height: 2px;
	width: 0px;
	background: transparent;
	transition: width .6s ease, background-color .6s ease;
}
.Navigation li:hover:after,
.Navigation li:hover:before {
	width: 100%;
	background: rgb(255, 255, 255);
}

/* // Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
	.nav-link a {
		font-size: 1.5rem;
	}
}

/* // Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
  .nav-link a {
		font-size: 1.5rem;
	}
}

/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .nav-link a {
		font-size: 1rem;
	}
}

.Jumbotron .display-1 {
	margin-bottom: 4rem;
	color: white;
	font-weight: 700;
}

.Jumbotron .display-1 {
	border-bottom: 3px solid var(--color-accent);
}

.Jumbotron .home-icon__wrapper {
	height: 64px;
	width: 64px;
	border-radius: 8px;
	position: absolute;
	top: 10px;
	left: 10px;
	background-color: white;
	transition: all 0.7s;
}

.Jumbotron .home-icon__wrapper:hover{
	background-color: var(--color-accent_3);
}

.Jumbotron .home-icon__wrapper img {
	height: 100%;
}

/* // Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
	.display-1 {
		font-size: 3.5rem;
	}
	.Jumbotron .home-icon__wrapper {
		height: 48px;
		width: 48px;
	}
}

/* // Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
	.display-1 {
		font-size: 2.5rem;
	}
	.Jumbotron .display-1 {
		margin-bottom: 2rem;
	}
	.Jumbotron .home-icon__wrapper {
		height: 32px;
		width: 32px;
	}
	
}

/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
	.display-1 {
		font-size: 1.5rem;
	}
	
}

.Home {
	height: 100vh;
}

.Home .gradient {
	background: linear-gradient(-45deg, rgb(57, 57, 57), var(--color-accent_2), var(--color-accent_3) ,rgb(216, 238, 252));
	background-size: 400% 400%;
	-webkit-animation: gradient 10s ease infinite;
	        animation: gradient 10s ease infinite;
	/* By:
  Manuel Pinto
  https://manuelpinto.in */
	height: 100vh;
}

.Home .Jumbotron .home-icon__wrapper{
	display: none;
}

@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.Home .container {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

.Home .Jumbotron {
	background-color: transparent;
	text-align: center;
}

.Home .Navigation li > a {
	color: rgba(255, 255, 255, 0.75);
	letter-spacing: var(--letter-spacing-small);
}

.Home .Jumbotron .display-1 {
	letter-spacing: var(--letter-spacing-big);
	margin-bottom: 7rem;
	color: white;
	text-shadow: black 3px 5px 2px;
	border-bottom: none;
	font-weight: 700;
}

.Home .Jumbotron .display-1 span {
	padding: 0.5rem;
	border-bottom: 3px solid rgba(255, 255, 255, 0.392);
}

/* // Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
}

/* // Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
	.Home .nav-links {
		flex-direction: column;
	}
	.Home .nav-link{
		padding: 1rem 0;
	}
	.Home .Jumbotron .display-1 {
		margin-bottom: 2rem;
		text-shadow: black 1px 3px 1px;
	}
}

/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
	.Home .Jumbotron{
		margin-top: -50%;
	}
	
}

